:root {
  --color-dark: #262626;
  --color-light: #fff;
  --color-green-1: #00654e;
  --color-green-2: #006e55;
  --color-green-3: #005844;
  --color-green-4: #006f55;
  --main-bg-color: #005844;
  --second-bg-color: #c0b487;
  --color-button: #c0b487;
  --color-button-trigger: #e5ab39;
  --border: 1px solid var(--color-button);
  --border-radius: 8px;
  --border-radius-small: 6px;
  --border-radius-large: 10px;
  --border-radius-large-big: 20px;
  --border-radius-modal: 14px;
  --shadow: 4px 7px 20px 0 var(--color-dark);
  --font-family-base: "Arial", sans-serif;
  --font-family-alt: "Cormorant", serif;
  --url-features-one: url("../img/icon/tree.svg");
  --url-features-two: url("../img/icon/christmas-decorations.svg");
  --url-features-three: url("../img/icon/flowers.svg");
  --url-features-four: url("../img/icon/delivery-truck.svg");
  --container-width: 1290px;
  --container-padding-x: 20px;
  --section-padding-y: 40px;
  --button-heigth: 51px;
  --transition-duration: 0.2s;
}
@media (max-width: 1024.98px) {
  :root {
    --container-padding-x: 42px;
  }
}
@media (max-width: 580.98px) {
  :root {
    --container-padding-x: 39px;
  }
}
@media (max-width: 580.98px) {
